import {Link} from '../routes'

import { Typography, Toolbar, Button, AppBar } from '@material-ui/core';
import lightblue from '@material-ui/core/colors/blueGrey';

import PropTypes from 'prop-types';

class Header extends React.Component {

    render() {
  
      const { classes } = this.props;
  
      return (
        <div style={{backgroundColor: lightblue[20]}}>
            <div className={classes.innerContainer}>
                <div className={classes.contentSection}>
                    <Grid container wrap="nowrap" spacing={32} className={classes.bigLineSpacing}>
                        <Grid item xs={4}>
                        </Grid>
                    </Grid>
                </div>
            </div>
        </div>
      )
    }
}

Header.propTypes = {
    classes: PropTypes.object.isRequired
  };

export default Header


